import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
import {Router} from '@angular/router';
import {InventoryServiceService} from '../inventory/inventory-service.service';

//import {InentoryIan} from '../virtual-inventory/inentory-ian';

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.scss']
})
export class InventoryComponent implements OnInit  {

  private page: number = 0;
  private inentoryIan: Array<any>;
  pages: Array<number>;
  //sorting
  key: string = 'skuCode';
  reverse: boolean = false;
  sort(key){
    this.key = key;
    this.reverse = !this.reverse;
  }
  p: number = 1;

  constructor(private _inventoryService: InventoryServiceService, private _router: Router) { 
    
  }

  ngOnInit() {
    this.getlistIanOther();

  }
  getlistIanOther() {
    this._inventoryService.getlistIanOther().subscribe(
      data => {
        console.log(data);
        this.inentoryIan = data['content'];
        this.pages = new Array(data['totalPages']);

        
      },
      (error) => {
        console.log(error.error.message);
      }
    );
  }

}
